import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from "react-redux";
import { changePicker } from "../../redux/actions/actions";
import DateRangePicker from 'react-bootstrap-daterangepicker';
import moment from 'moment';
import logo from '../../assets/img/LOGO_dashboard.png'

class TopHeader extends React.Component {
    constructor(props) {
      super(props)

      this.state = {
          label: '',
         startDate: moment().subtract(10, 'days'),
         endDate:moment().subtract(1, 'days'),
         locale:{
	        format: 'YYYY-MM-DD',
	        applyLabel: "确定",
	        cancelLabel: "取消",
	        customRangeLabel: "自定义",
        },
        ranges: {
	        '今天': [moment(), moment()],
	        '昨天': [moment().subtract(1, 'days'), moment().subtract(1, 'days')],
	        //'Last 7 Days': [moment().subtract(6, 'days'), moment()],
	        //'Last 30 Days': [moment().subtract(29, 'days'), moment()],
	        '本月': [moment().startOf('month'), moment().endOf('month')],
	        '上月': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')]
	    },
         fsTitle: '全屏显示'
      };
      

    }

    

    updateLabel = () => {
        let start = this.state.startDate.format('YYYY-MM-DD');
        let end = this.state.endDate.format('YYYY-MM-DD');
        let label = start + ' - ' + end;
        this.setState({label});

    }
    

    handleApply = (event, picker) => {
        let currentPicker = {
            startDate: picker.startDate ,
            endDate: picker.endDate
        };
        this.props.changePicker(currentPicker);
        this.setState({
            startDate: picker.startDate ,
            endDate: picker.endDate
        });
        this.updateLabel();
    }

    componentDidMount = () => {
        this.updateLabel();
    };
    
   

    render() {
        
        return (
            <div className="row border-bottom">
                <nav className="navbar" role="navigation" style={{marginBottom: 0}}>
                    <div className="navbar-header">
                    <div className="paddingleft30">
                        <img alt="" className="dashboard-logo img-responsive inline"  src={ logo } />
                        <h3 className=" inline">基于GPU的人工智能能效分析系统</h3>
                    </div>
                    </div>
                    <ul className="nav navbar-top-links navbar-right">
                        <li >
                            <DateRangePicker
                                opens="center"
                                autoApply={true}
                                showDropdowns={true}
                                ranges={this.state.ranges}
                                locale={this.state.locale}
                                startDate={this.state.startDate}
                                endDate={this.state.endDate}
                                onApply={this.handleApply}
                            >
                                {/* <button>Click Me To Open Picker!</button> */}
                                <input className="navdate"
                                    readOnly 
                                    type="text" 
                                    value={this.state.label}/>
                            </DateRangePicker>
                        </li>
                        <li >
                            <Link to="/dashboard"> <span className="nav-label">JQPT</span></Link>
                        </li>
                        <li >
                            <Link to="/shop/2"> <span className="nav-label">LGE</span></Link>
                        </li>
                        <li >
                            <Link to="/shop/3"> <span className="nav-label">GF6</span></Link>
                        </li>
                        <li>
                            
                        </li>
                        <li>
                            <a >
                                <i className="fa fa-sign-out"></i> Log out
                            </a>
                        </li>
                    </ul>
                </nav>
            </div>
        )
    }
}

export default connect(null, { changePicker })(TopHeader)